import { Button } from 'primereact/button';
import { Menu as PrimeMenu } from 'primereact/menu';
import React, { useRef } from 'react';

import { useMindmap } from '../use-mindmap';
import { CopyCutPasteActions } from '../use-mindmap-copy-cut-paste';
import { Menu, MenuVariantElementProps } from './use-tool-bar';

const buttonClasses = 'p-button-text';

const copyCutPasteMenuLarge = ({ hook }: MenuVariantElementProps) => {
  const values = hook();
  if (values?.type == 'node-cut-copy-paste') {
    const {
      actions: {
        onCopyClick,
        onPasteClick,
        onCutClick,
        onUncutClick,
        copySelectable,
        pasteSelectable,
        cutSelectable,
        uncutSelectable,
      },
    } = values;

    return (
      <>
        <div style={{ gridColumn: '1 / 2', gridRow: '1 / 2' }}>
          <Button
            onClick={onCopyClick}
            label="Copy"
            icon="pi ri-clipboard-line"
            className={buttonClasses}
            disabled={!copySelectable}
          />
        </div>
        <div style={{ gridColumn: '1 / 2', gridRow: '2 / 3' }}>
          <Button
            onClick={onCutClick}
            label="Cut"
            icon="pi ri-scissors-cut-line"
            className={buttonClasses}
            disabled={!cutSelectable}
          />
        </div>
        <div style={{ gridColumn: '2 / 3', gridRow: '1 / 2' }}>
          <Button
            onClick={onPasteClick}
            label="Paste"
            icon="pi ri-file-copy-line"
            className={buttonClasses}
            disabled={!pasteSelectable}
          />
        </div>
        <div style={{ gridColumn: '2 / 3', gridRow: '2 / 3' }}>
          <Button
            onClick={onUncutClick}
            label="Uncut"
            icon="pi ri-arrow-go-back-line"
            className={buttonClasses}
            disabled={!uncutSelectable}
          />
        </div>
      </>
    );
  }
  throw new Error('Unknown or missing hook or hook type.');
};

