import { Button } from 'primereact/button';
import { Menu as PrimeMenu } from 'primereact/menu';
import React, { useRef } from 'react';

import { useMindmap } from '../use-mindmap';
import { ManipulationActions } from '../use-mindmap-manipulation';
import { Menu, MenuVariantElementProps } from './use-tool-bar';

const buttonClasses = 'p-button-text';

const menu1Large = ({ hook }: MenuVariantElementProps) => {
  const values = hook();
  if (values?.type == 'node-manipulation') {
    const {
      actions: {
        onDeleteClick,
        onAddChildClick,
        onAddTopSiblingClick,
        onAddBottomSiblingClick,
        addBottomSiblingSelectable,
        addTopSiblingSelectable,
        addChildSelectable,
        deleteSelectable,
      },
    } = values;

    return (
      <>
        <div style={{ gridColumn: '1 / 2', gridRow: '1 / 2' }}>
          <Button
            onClick={onAddTopSiblingClick}
            label="Nachbarsknoten"
            icon="pi pi-angle-up"
            className={buttonClasses}
            disabled={addTopSiblingSelectable}
          />
        </div>
        <div style={{ gridColumn: '2 / 3', gridRow: '1 / 2' }}>
          <Button
            onClick={onAddChildClick}
            label="Unterknoten"
            icon="pi pi-angle-right"
            className={buttonClasses}
            disabled={addChildSelectable}
          />
        </div>
        <div style={{ gridColumn: '1 / 2', gridRow: '2 / 3' }}>
          <Button
            onClick={onAddBottomSiblingClick}
            label="Nachbarsknoten"
            icon="pi pi-angle-down"
            className={buttonClasses}
            disabled={addBottomSiblingSelectable}
          />
        </div>
        <div style={{ gridColumn: '2 / 3', gridRow: '2 / 3' }}>
          <Button
            onClick={onDeleteClick}
            label="Knoten löschen"
            icon="pi pi-times"
            className={`${buttonClasses} p-button-danger`}
            disabled={deleteSelectable}
          />
        </div>
      </>
    );
  }
  throw new Error('Unknown or missing hook or hook type.');
};

