const menu1Small = ({ hook }: MenuVariantElementProps) => {
  const values = hook();
  if (values?.type == 'node-manipulation') {
    const {
      actions: {
        onDeleteClick,
        onAddChildClick,
        onAddTopSiblingClick,
        onAddBottomSiblingClick,
        addBottomSiblingSelectable,
        addTopSiblingSelectable,
        addChildSelectable,
        deleteSelectable,
      },
      menuRef,
    } = values;

    const items = [
      {
        label: 'Nachbarsknoten',
        icon: 'pi pi-angle-up',
        command: onAddTopSiblingClick,
        disabled: addTopSiblingSelectable,
      },
      {
        label: 'Nachbarsknoten',
        icon: 'pi pi-angle-down',
        command: onAddBottomSiblingClick,
        disabled: addBottomSiblingSelectable,
      },
      {
        label: 'Unterknoten',
        icon: 'pi pi-angle-right',
        command: onAddChildClick,
      },
    ];

    const onMenuClick = (event: any) => {
      // @ts-ignore
      menuRef.current.toggle(event);
    };

    return (
      <>
        <div style={{ gridColumn: '1 / 2', gridRow: '1 / 2' }}>
          <PrimeMenu model={items} ref={menuRef} popup id="node_manipulation_popup_menu" />
          <Button
            label="Knoten"
            icon="pi pi-plus"
            className={buttonClasses}
            disabled={addChildSelectable}
            onClick={onMenuClick}
            aria-controls="node_manipulation_popup_menu"
            aria-haspopup
          />
        </div>
        <div style={{ gridColumn: '1 / 2', gridRow: '2 / 3' }}>
          <Button
            label="Löschen"
            icon="pi pi-cross"
            onClick={onDeleteClick}
            className={`${buttonClasses} p-button-danger`}
            disabled={deleteSelectable}
          />
        </div>
      </>
    );
  }
  throw new Error('Unknown or missing hook or hook type.');
};

export interface NodeManipulationHookValue {
  type: 'node-manipulation';
  actions: ManipulationActions;
  menuRef: React.MutableRefObject<null>;
}

export const nodeManipulationMenu: Menu = {
  id: 'menu-manipulation',
  variants: [
    { Element: menu1Small, priority: 10 },
    { Element: menu1Medium, priority: 10 },
    { Element: menu1Large, priority: 10 },
  ],
  currentVariant: 2,
  hook: () => {
    const menuRef = useRef(null);
    const {
      manipulation: { actions },
    } = useMindmap();

    return {
      type: 'node-manipulation',
      actions,
      menuRef,
    };
  },
  tempHook: () => ({
    type: 'node-manipulation',
    actions: {
      addBottomSiblingSelectable: false,
      addChildSelectable: false,
      addTopSiblingSelectable: false,
      deleteSelectable: false,
      addAttachementSelectable: false,
      onAddBottomSiblingClick: () => {},
      onAddChildClick: () => {},
      onAddTopSiblingClick: () => {},
      onDeleteClick: () => {},
      onAddLinkAttachementClick: () => {},
      onAddFileAttachementClick: () => {},
    },
    menuRef: React.createRef(),
  }),
};
